<template>
  <div class="course-sign">
    <head-top title="课堂签到">
      <span slot="right" class="head-right" @click="toNewSign">
       <span class="head_span">二次签到 </span>
      </span>
    </head-top>

    <div class="sub_nav">
      <span>签到模式</span>

      <van-radio-group v-model="signMode" direction="horizontal" @change="changeSignWay" :style="{width: 'unset'}">
        <van-radio v-for="(item, index) in options" :key="index" :name="item.value">{{item.text}}</van-radio>
      </van-radio-group>
    </div>
    <div class="sign_code" v-show="signMode===1">
      <p class="sign_remark">学生点击签到即可签到</p>
    </div>
    <div class="sign_code" v-show="signMode===2">
      <canvas id="signCode"></canvas>
      <p class="sign_remark">签到码：{{codeValue}}</p>
    </div>

    <div class="sub_nav">
      <span>签到统计</span>

      <van-dropdown-menu>
        <van-dropdown-item v-model="signID" :options="signStatistic" @change="changeSignTime"/>
      </van-dropdown-menu>
    </div>
    <div class="sign_List">
      <van-circle v-model="currentRate" :rate="rate" :stroke-width="60" :text="currentNum + '人已签'"
                  layer-color="#f3f3f3" :clockwise="false">
      </van-circle>
    </div>

    <div class="sub_nav">
      <span>签到列表</span>
      <van-button type="primary" size="small" @click="changeSignTime(signID)">刷新</van-button>
    </div>

    <van-cell-group v-if="personList && personList.length>0">
      <van-cell class="cell_main" v-for="(item, index) in personList" :key="index">
        <template #icon>
          <img :src="item.imgSrc" alt=""/>
        </template>
        <template #title>
          <span class="line_title">{{item.name}}</span>
        </template>
        <template #label>
          <span class="line_title">{{item.sno}}</span>
        </template>
        <template #right-icon>
          <van-tag plain type="primary" v-if="item.remark">{{item.remark}}</van-tag>
          <van-tag plain v-else>未签到</van-tag>
        </template>
      </van-cell>
    </van-cell-group>
    <r-lack-data v-else></r-lack-data>
  </div>
</template>

<script>
import QRCode from "qrcode";
import defaultImg from "../../../../assets/boy_img.png";
import { Dialog } from "vant";

export default {
  name: "signWay",
  data () {
    return {
      signMode: 1,
      searchSignMode: -1,
      options: [
        { text: '自动签到', value: 1 },
        { text: '扫码签到', value: 2 }
      ],
      signID: "",
      signStatistic: [],
      codeValue: "0591",

      currentRate: 0,
      currentNum: 0,
      rate: 0,
      personList: [],
      isLoading: false
    };
  },
  mounted () {
    this.getSignMode();
    this.getSignStatistic();
  },
  methods: {
    getSignMode () {
      this.$gatewayHttp.sign.getSignMode().then(res => {
        if (res.Result) {
          this.signMode = res["SignModel"];
          this.codeValue = res["SignCode"];
          this.searchSignMode = res["SignModel"];
        } else if (res.Info) {
          this.$toast(res.Info);
        }
      }).catch(() => {
      });
    },
    getSignStatistic () {
      this.signStatistic = [];
      this.$gatewayHttp.sign.getDataStatistic().then(res => {
        if (res.Result) {
          if (res.Datas && res.Datas.length > 0) {
            res.Datas.sort((a, b) => {
              return a["Number"] > b["Number"] ? -1 : 1;
            });
            res.Datas.forEach(item => {
              this.signStatistic.push({
                text: `第${item["Number"]}次签到`,
                value: item["SNID"],
                number: item["Number"],
                courseID: item["CIID"]
              });
            });
            this.signID = res.Datas[0]["SNID"];
            this.changeSignTime(this.signID);
          }
        } else if (res.Info) {
          this.$toast(res.Info);
        }
      }).catch(() => {
      });
    },
    changeSignTime (value) {
      let signInfo = this.signStatistic.find(r => r.value === value);
      this.personList = [];
      this.currentNum = 0;
      this.rate = 0;
      this.isLoading = true;
      this.$gatewayHttp.sign.getDataList({
        number: signInfo.number
      }).then(res => {
        if (res.Result) {
          if (res.Datas && res.Datas.length > 0) {
            res.Datas.sort((a, b) => {
              return a["SignTime"] > b["SignTime"] ? -1 : 1;
            });
            res.Datas.forEach(item => {
              this.personList.push({
                imgSrc: defaultImg,
                name: item.Key["StudentName"],
                id: item.Key["StudentID"],
                sno: item.Key["StudentXH"],
                remark: item["SignTime"]
              });
              if (item["SignTime"]) this.currentNum++;
            });
          }
        } else if (res.Info) {
          this.$toast(res.Info);
        }
        this.rate = this.personList.length > 0 ? this.currentNum * 100.0 / this.personList.length : 0;
        this.isLoading = false;
      }).catch(() => {
        this.isLoading = false;
      });
    },
    toNewSign () {
      Dialog.confirm({
        message: "确定再次签到吗？"
      }).then(() => {
        this.addSign();
      }).catch(() => {
      });
    },
    addSign () {
      this.$gatewayHttp.sign.getSign().then(res => {
        if (res.Result) {
          this.$toast("启动成功");
          this.getSignStatistic();
        } else if (res.Info) {
          this.$toast(res.Info);
        } else {
          this.$toast("启动失败");
        }
      }).catch(() => {
        this.$toast("操作失败");
      });
    },
    changeSignWay (value) {
      // console.log(value);
      if (value === 2) {
        setTimeout(() => {
          this.drawCode();
        }, 0);
      }
      // 首次查询签到码，不触发操作事件
      if (this.searchSignMode === value) {
        this.searchSignMode = -1;
        return;
      }

      this.$gatewayHttp.sign.setSignMode({
        signModel: value
      }).then(res => {
        if (res.Result) {
          this.$toast("切换成功");
        } else if (res.Info) {
          this.$toast(res.Info);
        } else {
          this.$toast("切换失败");
        }
      }).catch(() => {
        this.$toast("操作失败");
      });
    },
    drawCode () {
      QRCode.toCanvas(
        document.getElementById("signCode"),
        JSON.stringify({
          CIID: this.$store.getters.courseID,
          SignCode: this.codeValue
        }, null, "\t"),
        {
          errorCottectionLevel: "H",
          margin: 1,
          width: 300
        },
        error => {
          if (error) {
            console.log(error);
          }
        }
      );
    }
  }
}
</script>

<style lang="less" scoped>
@import "./../../../../styles/common/base";
.course-sign {
  min-height: 100%;
  background-color: #fff;

  /deep/ .van-dropdown-menu {
    background-color: #f6f7fa;
  }

  .sign_code {
    .sign_remark {
      font-size: @font-title;
      line-height: 2;
      color: #00cbab;
    }
  }

  .sign_List {
    margin: 20px auto;
  }
}
</style>
